@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap");
:root {
  --red: #8e0404;
  --white: #fff;
  --black: rgb(0, 0, 30);
}

:root {
  --font-Sans: "Open Sans", sans-serif;
  --font-Poppins: "Poppins", sans-serif;
  --font-Normal: 400;
  --font-semiBold: 600;
  --font-Bold: 700;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

body {
  position: relative;
  font-family: var(--font-Poppins);
  font-size: 16px;
  overflow-x: hidden;
}

img {
  height: auto;
  max-width: 100%;
}

ul,
ol {
  list-style: none;
}

button,
input {
  font-family: var(--fontNormal);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  max-width: 1100px;
  width: 100%;
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
p {
  font-family: var(--font-Sans);
}

.heading, .heading--xxl {
  color: var(--black);
  font-weight: 400;
}
.heading__name {
  color: var(--red);
}
.heading--xxl {
  font-size: clamp(34px, 6vw, 53px);
  max-width: 430px;
  font-weight: 700;
  margin-bottom: 20px;
}
.heading--xl {
  position: relative;
  font-size: clamp(20px, 4vw, 30px);
  font-weight: 700;
  text-align: center;
  color: var(--red);
  margin-bottom: 29px;
}
.heading--xl::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: -5px;
  height: 5px;
  width: 64px;
  margin: 0 auto;
  background-color: var(--red);
}

.paragraph, .paragraph--l {
  line-height: 1.2;
}
.paragraph__name {
  color: var(--black);
  font-weight: 700;
}
.paragraph--l {
  font-size: clamp(23px, 3vw, 27px);
  margin-bottom: 26px;
}
.paragraph--about {
  font-size: 15px;
  margin-bottom: 17px;
  line-height: 24px;
}

.btn, .btn--start, .btn--blue, .btn--menu {
  position: relative;
  outline: none;
  border: none;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 300ms, border 300ms ease-in;
}
.btn--menu {
  background-color: transparent;
  z-index: 1;
}
@media screen and (min-width: 769px) {
  .btn--menu {
    display: none;
  }
}
.btn--blue {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--red);
  border: 2px solid var(--red);
  border-radius: 10px;
  color: var(--white);
  box-shadow: 0px 10px 15px -7px var(--black);
}
.btn--blue:hover {
  background-color: var(--white);
  color: var(--red);
}
.btn--start {
  width: 180px;
  height: 48px;
}

.header {
  position: absolute;
  left: 0;
  width: 100%;
  padding: 20px 18px 0;
  z-index: 999;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 51px;
}
.header__logo {
  z-index: 1;
  font-size: 24px;
  color: var(--black);
}
.header__logo--letter {
  font-size: 30px;
  color: var(--red);
}
@media screen and (max-width: 768px) {
  .header__menu {
    position: fixed;
    width: 80%;
    top: 70px;
    right: -100%;
    height: calc(100vh - 70px);
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--black);
    transition: all 500ms ease-in;
    border-top-left-radius: 50px;
  }
  .header__menu.is-active {
    right: 0px;
  }
}
.header__list {
  display: flex;
  align-items: center;
}
.header__item:not(:last-child) {
  margin-right: 20px;
}
@media screen and (max-width: 768px) {
  .header__list {
    display: grid;
    grid-template-columns: max-content;
    grid-template-rows: repeat(5, 3.75rem);
    align-items: center;
    justify-items: center;
  }
}
.header__link {
  position: relative;
  font-size: 17px;
  font-weight: 600;
  color: var(--white);
  text-transform: uppercase;
}
.header__link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  height: 4px;
  width: 100%;
  background-color: var(--red);
  visibility: hidden;
  opacity: 0;
  transform: scale(0);
}
@media screen and (min-width: 769px) {
  .header__link {
    color: var(--black);
    font-size: 15px;
  }
  .header__link:hover {
    color: var(--red);
  }
  .header__link:hover::before {
    visibility: visible;
    opacity: 1;
    transform: scale(1);
    transition: all 350ms ease-in;
  }
}

.hero {
  position: relative;
  padding: 0 18px;
}
.hero__container {
  display: flex;
  flex-direction: column;
  padding: 166px 0 140px 0;
}
@media screen and (min-width: 769px) {
  .hero__container {
    justify-content: center;
    align-items: center;
    flex-direction: row;
  }
}
.hero__text {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
@media screen and (min-width: 769px) {
  .hero__text {
    align-items: flex-start;
    flex: 1 0 50%;
    text-align: left;
  }
}

.about {
  padding: 0 18px;
}
.about__container {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  align-items: center;
}
@media screen and (min-width: 769px) {
  .about__container {
    grid-template-columns: 380px 1fr;
  }
}
.about__img {
  width: 200px;
  height: 200px;
}
@media screen and (min-width: 769px) {
  .about__img {
    width: 270px;
    height: 270px;
  }
}